a {
    text-decoration: none;
    color: black;
}
.items-page{
    background: linear-gradient(45deg, rgba(127,0,255, 0.4), rgba(225,0,255, 0.4));
    height: 100%;
}
.item-avatar{
    height: 60px;
    border-radius: 50%;
    border: 3px solid grey;
}
.items-list--holder{
    display: grid;
    grid-template-columns: 1fr;
    box-sizing: border-box;
    padding: 20px;
    width: 100%;
    row-gap: 20px;
    max-width: 1000px;
}
.item-component--holder{
    height: 100%;
    width: 100%;
}
@media (min-width: 1000px){
    .items-list--holder{
        grid-template-columns: 1fr 1fr;
        column-gap: 20px;
    }
    
}